.clicker{
    height: 4%;
    width: 3%;
    position: fixed;
    right: 0;
    bottom: 0;
}

.container{
    background-color: rgba(42,43,42,255);
    color: rgb(176,176,183);
    border-radius: 30%;
    text-align: center;
    padding: 3%;
    transition: all 0.5s ease-out;
}

.container:hover{
    background-color: rgb(65,64,64);
    color: white;
    transition: all .3s ease-in;
    
}

